  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden; }

:root {
  box-sizing: border-box;
  font-size: 20px; }

@media all and (max-width: 768px) {
  :root {
    font-size: 15px; } }

*,
::before,
::after {
  box-sizing: inherit; }

html,
body {
  overflow-x: hidden;
  background-color: #000; }

.page {
