.photo_links {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 100%;
  position: relative;
  overflow: hidden; }

.links_item {
  position: relative;
  display: block;
  overflow: hidden;
  width: 30%;
  min-width: 300px;
  min-height: 70vh;
  margin: 5px;
  flex-grow: 1;
  flex-shrink: 1;
  cursor: pointer;
  transition: all 0.4s ease-in-out; }

@media all and (min-width: 768px) {
  .links_item {
    min-height: 50vh; } }

.links_item::before {
  display: block;
  position: absolute;
  z-index: 99;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  content: "";
  background-color: rgba(0, 0, 0, 0.4); }

.links_item:hover {
  transform: scale(1.03); }

.links_item:active {
  transform: scale(1); }

.links_item_title {
  z-index: 100;
  position: absolute;
  font-size: 1.8rem;
  left: 50%;
  top: 40%;
  transform: translate(-50%, -50%);
  color: rgba(255, 255, 255, 0.9); }

.links_item img {
  display: block;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  object-fit: cover; }

